@media screen and (min-width:320px){
    .Areas {
      padding: 10px;
      max-width: 318px;
      width: 50%;
    }
  }
  
  @media screen and (min-width:480px){
    .Areas {
      padding: 1em;
      height: 100%;
      width: 100%;
    }
  }
  
  @media screen and (min-width:768px){
    .Areas {
      padding: 2em;
      height: 100%;
      width: 100%;
    }
  }
  
  @media screen and (min-width:1024px){
    .Areas {
      padding: 2em;
      height: 100%;
      width: 100%;
    }
  }


